import h from '../framework/h'
import ArticleDescription from '../articles/ArticleDescription'

export default (x: number, y: number, article: ArticleDescription) => {
    const radius = 35 // in pixels
    const a = "df"
    return h(
    'div', 
    {
        class: "article-node",
        style: `
            left: calc(${x * 100}vw - ${radius}px);
            top:  calc(${y * 100}vh - ${radius}px);
        `,
    }, [
        h('img', 
        {
            class: "article-thumbnail",
            style: `
                width:  ${radius*2}px;
                height: ${radius*2}px;
            `,
            onclick: "window.location.href = 'articles/"+ article.folderName +"/index.html'",
            src: article.thumbnail
        }),
        h('p',
        {
            class: "article-infos",
            style: `
                height: ${radius*2}px;
                left: ${radius}px;
            `,
        },
            article.title
        )
    ])
}